import style from "../../FinanceManager/FinanceManager.module.css";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useLocation, useParams, useSearchParams } from "react-router-dom";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import { Suspense } from "react";
import QvipleLoader from "../../../Loader/QvipleLoader";
import PaymentVerificationRequest from "./PaymentVerificationRequest";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import PaymentVerificationRejected from "./PaymentVerificationRejected";
import PaymentVerificationAccepted from "./PaymentVerificationAccepted";

const PaymentVerification = ({
  aid,
  requestedCount,
  approvedCount,
  rejectCount,
}) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const params = useParams();
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("fee_receipts_request")}</h6>
            <img
              src={`${assestsUserFinanceUrl}/filter.svg`}
              alt="filter icon"
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <HorizontalTabWrapper>
            {tabList.map((tab) => (
              <HorizontalTabCard
                key={tab?.id}
                tab={tab}
                isLink
                isActive={
                  tab.id === "requested" && !searchParams.get("n")
                    ? true
                    : searchParams.get("n") === tab.id
                    ? true
                    : false
                }
                parentState={getQuery.state}
                url={
                  getQuery.state?.urlOffSet === "Institute"
                    ? `/q/${params.username}/institute/member/finance?type=verification${tab.urlKey}`
                    : `/q/${params.username}/member/staff/finance?type=verification${tab.urlKey}`
                }
              />
            ))}
          </HorizontalTabWrapper>
          <BorderBottom
            customStyleBorder={{
              marginTop: "0",
            }}
          />
        </UserMemberContentWrapper>
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <Suspense fallback={<QvipleLoader />}>
            {searchParams.get("n") === "accepted" ? (
              <PaymentVerificationAccepted
                aid={aid}
                approvedCount={approvedCount}
              />
            ) : searchParams.get("n") === "rejected" ? (
              <PaymentVerificationRejected
                aid={aid}
                rejectCount={rejectCount}
              />
            ) : (
              <PaymentVerificationRequest
                aid={aid}
                requestedCount={requestedCount}
              />
            )}
          </Suspense>
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default PaymentVerification;

const tabList = [
  {
    label: "all_request",
    id: "requested",
    urlKey: "",
  },
  {
    label: "accepted",
    id: "accepted",
    urlKey: "&n=accepted",
  },
  {
    label: "rejected",
    id: "rejected",
    urlKey: "&n=rejected",
  },
];
