import { useTranslation } from "react-i18next";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import styleFinance from "../../FinanceManager/Income/Income.module.css";
import { useState } from "react";
import RectangleTabWrapper from "../../../Ui/Tab/Rectangle/RectangleTabWrapper";
import RectangleTabCard from "../../../Ui/Tab/Rectangle/RectangleTabCard";
import style from "./PaymentVerification.module.css";
import dayjs from "dayjs";
import FormInput from "../../../Ui/Input/FormInput";
import { useCallback } from "react";
import { cdnUrl } from "../../../Services/UrlConfig/BaseUrl";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import { useAdmissionPaymentVerifyAction } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import CreateButton from "../../../Ui/Button/CreateButton";
const PaymentVerificationCardAction = ({
  onClose,
  aid,
  onRefetch,
  receipt,
  isAccess,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState(1);
  const [state, setState] = useState("");
  const [errorField, setErrorField] = useState("");

  const [admissionPaymentVerifyAction] = useAdmissionPaymentVerifyAction();

  const onAccept = () => {
    setStep(1);
  };

  const onReject = () => {
    setStep(2);
  };
  const onInputChange = () => {};

  const onViewAcknowledge = useCallback(() => {
    const newWindow = window.open(
      `${cdnUrl}/${receipt?.receipt?.fee_payment_acknowledge}`,
      "_blank",
      "noopener,noreferrer"
    );
    if (newWindow) newWindow.opener = null;
  }, [cdnUrl, receipt?.receipt?.fee_payment_acknowledge]);

  const onActionFire = () => {
    let validation = true;
    if (step === 2) validation = requiredField({ reason: state }, []);
    if (existingRequiredField(validation)) {
      if (aid && receipt?.receipt?._id && receipt?._id) {
        setDisabled((pre) => !pre);
        admissionPaymentVerifyAction({
          aid: aid,
          rid: receipt?.receipt?._id,
          reqId: receipt?._id,
          status:
            isAccess === "REJECTED"
              ? step === 1
                ? "Over_Rejection"
                : "Rejection_Notify"
              : step === 1
              ? "Approved"
              : "Rejected",
          receiptAction: { reason: state },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={styleFinance.modal_container}>
          <UserMemberContentWrapper>
            <div className={styleFinance.modal_container_outer_header}>
              <div className={styleFinance.modal_container_header}>
                <h6> {t("fee_receipt_request")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
              marginBottom: "1rem",
            }}
          >
            <RectangleTabWrapper>
              <RectangleTabCard
                label="accept"
                onClick={onAccept}
                isActive={step === 1 ? true : false}
              />
              <RectangleTabCard
                label="reject"
                onClick={onReject}
                isActive={step === 2 ? true : false}
              />
            </RectangleTabWrapper>

            <section className={style.fee_request_detail}>
              <h6 className={style.fee_request_detail_name}>
                {t("name")} {" : "}{" "}
                {`${receipt?.receipt?.student?.studentFirstName} ${
                  receipt?.receipt?.student?.studentMiddleName
                    ? receipt?.receipt?.student?.studentMiddleName
                    : ""
                } ${receipt?.receipt?.student?.studentLastName}`}
              </h6>
              <h6 className={style.fee_request_detail_amount}>
                {t("amount")} {" : "} {t("rs")}{" "}
                {receipt?.receipt?.fee_payment_amount ?? 0}
              </h6>
              <h6 className={style.fee_request_detail_date}>
                {dayjs(receipt?.receipt?.created_at).format("DD MMM YYYY")}
              </h6>
            </section>
            {step === 1 ? (
              <>
                <FormInput
                  label={t("bank_name_collect")}
                  name={"fee_bank_name"}
                  type={"tel"}
                  value={receipt?.receipt?.fee_bank_name}
                  onChange={onInputChange}
                  placeholder={t("please_enter_bank_name")}
                  customStyleLabel={{
                    marginTop: "6px",
                    marginBottom: "7px",
                  }}
                />
                <FormInput
                  label={t("banke_holder_name")}
                  name={"fee_bank_holder"}
                  type={"tel"}
                  value={receipt?.receipt?.fee_bank_holder}
                  onChange={onInputChange}
                  placeholder={t("please_enter_name_of_bank_holder")}
                  customStyleLabel={{
                    marginTop: "6px",
                    marginBottom: "7px",
                  }}
                />
                <FormInput
                  label={t("cheque_dd_neft")}
                  name={"fee_utr_reference"}
                  type={"tel"}
                  value={receipt?.receipt?.fee_utr_reference}
                  onChange={onInputChange}
                  placeholder={t("please_enter_utr_ref_number")}
                  customStyleLabel={{
                    marginTop: "6px",
                    marginBottom: "7px",
                  }}
                />

                {receipt?.receipt?.fee_payment_acknowledge ? (
                  <button
                    className={style.payment_acknowledge_btn}
                    onClick={onViewAcknowledge}
                  >
                    {t("payment_acknowledge")}
                  </button>
                ) : null}
              </>
            ) : (
              <FormInput
                label={t("reason_for_rejection_receipt")}
                name="reason"
                type="text"
                value={state}
                onChange={(e) => setState(e.target.value)}
                placeholder={t("please_reason_for_rejection_receipt")}
                errorShow={errorField["reason"]}
              />
            )}

            <CreateButton
              label={step === 1 ? "confirm" : "confirm_reject"}
              onAction={onActionFire}
            />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default PaymentVerificationCardAction;
