import { useTranslation } from "react-i18next";
import style from "./PaymentVerification.module.css";
import { useState } from "react";
import { useCallback } from "react";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
import CreateButton from "../../../Ui/Button/CreateButton";
import PaymentVerificationCardAction from "./PaymentVerificationCardAction";

const PaymentVerificationCard = ({ receipt, isAccess, aid, onRefetch }) => {
  const { t } = useTranslation();
  const [openReceipt, setOpenReceipt] = useState(false);

  const onReceiptToggle = useCallback(() => {
    if (isAccess === "REQUEST") setOpenReceipt((pre) => !pre);
    else if (isAccess === "REJECTED" && receipt?.receipt?.re_apply) {
      setOpenReceipt((pre) => !pre);
    } else {
    }
  }, [isAccess, receipt?.receipt?.re_apply]);

  const onNull = () => {};
  return (
    <>
      <section
        className={style.request_card_container}
        onClick={receipt?.receipt?.re_apply ? onNull : onReceiptToggle}
        style={{
          cursor: isAccess === "REQUEST" ? "pointer" : "default",
        }}
      >
        <div className={style.request_card_info}>
          <img
            src={
              receipt?.receipt?.student?.studentProfilePhoto
                ? `${imageShowUrl}/${receipt?.receipt?.student?.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            alt="avatar"
            className={style.request_card_info_image}
          />
          <div>
            <h6 className={style.request_card_info_name}>
              {`${receipt?.receipt?.student?.studentFirstName ?? ""} ${
                receipt?.receipt?.student?.studentMiddleName
                  ? receipt?.receipt?.student?.studentMiddleName
                  : ""
              } ${receipt?.receipt?.student?.studentLastName ?? ""}`}
            </h6>
            <p className={style.request_card_info_name}>
              {t("amount")} : {t("rs_only")}{" "}
              {receipt?.receipt?.fee_payment_amount || 0}
            </p>
            <p className={style.request_card_info_date}>
              {dayjs(receipt?.created_at).format("DD MMM YYYY")}
            </p>
          </div>
        </div>
        <h6 className={style.request_card_utr}>
          {t("utr_number")} {receipt?.receipt?.fee_utr_reference}
        </h6>
        {isAccess === "REJECTED" && (
          <>
            <h6 className={style.reason_for_rejection}>
              {t("reason_for_rejection")}{" "}
              {receipt?.receipt?.reason ?? receipt?.reason}
            </h6>
            {receipt?.receipt?.re_apply && (
              <CreateButton label="re_requested" onAction={onReceiptToggle} />
            )}
          </>
        )}
      </section>
      {openReceipt && (
        <PaymentVerificationCardAction
          onClose={onReceiptToggle}
          aid={aid}
          receipt={receipt}
          onRefetch={onRefetch}
          isAccess={isAccess}
        />
      )}
    </>
  );
};

export default PaymentVerificationCard;
